<template>
<div class="inputoutline">
    <div class="inputoutline-title">{{title}}</div>
    <input type="text" @input="changecontent" v-model="content">
</div>
</template>

<script>
export default {
    props:['title','currentindex'],
    data() {
        return {
            content:'',
            index:this.currentindex
        }
    },
    methods:{
        changecontent(){
            this.$emit('emitcontent',this.content,this.index)
        }
    }
}
</script>

