<template>
<div class="ticket">
    <div class="ticketinfo">
        <div class="date">
            <span>2/19（五）</span>
        </div>
        <div class="time">
            <span>中午12:00 - 下午2:00</span>
        </div>
    </div>

    <div class="buybtn">
        <i class="ri-add-line" @click="add"></i>
        <div></div>
        <i class="ri-subtract-line" @click="minus"></i>
    </div>
    <div class="ticketnumber" v-show="numbershow">
        <span>{{ticketnumber}}</span>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            ticketnumber: 0,
            numbershow: false
        }
    },
    methods: {
        add() {

            // if (this.ticketnumber == 0) {
            //     this.numbershow = false
            //     this.ticketnumber == 0
            // } else {
            //     this.numbershow = true
            // }
            this.ticketnumber++;
            if(this.ticketnumber >0){
                this.numbershow = true
            }
            this.$store.commit('setticketnumber',this.ticketnumber)
            

        },
        minus() {
            this.ticketnumber--;
            if(this.ticketnumber <= 0){
                this.numbershow = false
                this.ticketnumber = 0
                this.$store.commit('setticketnumber',this.ticketnumber)
            }
            

        }
    }
}
</script>

